import React,{useState , useEffect} from 'react'
import { FiChevronRight, FiChevronLeft } from 'react-icons/fi';
import { FaQuoteRight } from 'react-icons/fa';
import data from './data'
import './style.css'

function Main() {
 const [peoples ] = useState(data);
 const [value , setValue] = useState(0)
 useEffect(()=>{
  let lastindex = peoples.length-1;
  if(value < 0){
   setValue(lastindex);
  }
  if(value > peoples.length-1){
   setValue(0)
  }
 },[value , peoples])

 useEffect(()=>{
   let timer = setInterval(()=>{
     setValue(value +1)
   },3000)
   return ()=>{clearInterval(timer);}
 },[value])
 useEffect(()=>{
   document.title='Slider'
 },[])
 return (
  <main>
   <div className="container">
    <div className="header">
     <h1><span>/</span>Reviews</h1>
    </div>
    <div className="sliderContainer">
     {/* ///////////////// */}
     {
      peoples.map((people , index)=>{
       const {id , image , title , quote , name} = people
       let position ='nextSlider';
       if(index === value){
        position ='activeSlider'
       }
       if(index === value - 1 || (value === 0 && index === peoples.length - 1)){
        position='lastSlider'
       }
       return(
        <div className={`oneSlider ${position}`} key={id}>
        <div className="imageContainer">
        <img src={image} alt={name}/>
     </div>
     <div className="detailscontainer">
      <h3>{name}</h3>
      <h4>{title}</h4>
     </div>
     <div className="info">
      <p>{quote}</p>
     </div>
     
     <div className="qiotes">
       <div><FaQuoteRight className='quet'/></div>
     </div>
     </div>
       )
      })
     }
     
     {/* /////////////////// */}
     <div className="sliderbuttons">
      <button className="prev btn" onClick={()=> setValue(value - 1)}><FiChevronLeft/></button>
      <button className="next btn" onClick={()=> setValue(value + 1)}><FiChevronRight/></button>
     </div>
    </div>
   </div>
  </main>
 )
}

export default Main
